body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0
}

@font-face {
  font-family: 'digital_dreamregular';
  src: url('/.assets/fonts/digitaldream-webfont.woff2') format('woff2'),
    url(../../static/media/digitaldream-webfont.ef2c8acb.woff2) format('woff');
  font-weight: normal;
  font-style: normal;

}

body {
  width: 100vw;
  height: 100vh;
  background: url("https://www.starregister.org/images/starsBg-wide.jpg") center/ cover no-repeat;
  overflow: hidden;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.date {
  -webkit-order: 0;
          order: 0;
  margin: 30px 0;
  font-size: 3rem;
  -webkit-align-self: center;
          align-self: center;
  margin-left: 100px;
  font-family: "digital_dreamregular", sans-serif;
  color: rgb(0, 247, 255);
}

.speed {
  margin-left: 30px;
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.station-info {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  padding-top: 50px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.meter {
  font-size: 2rem;
  display: block;
  font-family: "digital_dreamregular", sans-serif;
  color: white;
}

.distance {
  margin-left: 30px;
  -webkit-align-self: flex-end;
          align-self: flex-end;
}
.planet-shadow {
	width: 40vw;
	height: 40.1vw;
	position: absolute;
	border-radius: 50%;
	margin-top: -40vw;
	box-shadow: inset -5px 0px 80px -10px rgb(0, 204, 255)
}

.planet-box {
	position: relative;
	width: 40vw;
	height: 39vw;
	margin-bottom: 100px;
	border-radius: 50%;
	overflow: hidden;
}

.planet {
	height: 100%;
	/* margin-left: calc(-105% + 28.69%) */
	/* -0.55% / minute */
	/* long / -0.55% */
}

.station {
	width: 5vw;
	height: 5vw;
	position: absolute;
	left: 49%;
	top: calc(50% + 8.3%)
}

@-webkit-keyframes Spin {
	0% {
		margin-left: -60%
	}

	100% {
		margin-left: -260%
	}
}

@keyframes Spin {
	0% {
		margin-left: -60%
	}

	100% {
		margin-left: -260%
	}
}
